import React, { useState, useContext } from 'react'
import { Link, useHistory } from 'react-router-dom'
import { signOut } from '../../../../core/services/auth-service'
import {
  Collapse,
  Navbar,
  NavbarToggler,
  Nav,
  NavItem,
  Dropdown,
  DropdownToggle,
  DropdownMenu,
  DropdownItem,
  Container,
  InputGroup,
  InputGroupText,
  InputGroupAddon,
  Input
} from 'reactstrap'
import PageContext from '../page/page-context'

export default function NavBar () {
  const history = useHistory()
  const sidebarToggle = React.createRef()
  const [color, setColor] = useState('transparent')
  const [isOpen, setIsOpen] = useState(false)
  const [dropdownNotificationsOpen, setDropdownNotificationsOpen] = useState(false)
  const [dropdownOptionsOpen, setDropdownOptionsOpen] = useState(false)
  const { pageTitle, pageTitleOptions } = useContext(PageContext)

  function openSidebar () {
    document.documentElement.classList.toggle('nav-open')
    sidebarToggle.current.classList.toggle('toggled')
  }

  function toggle () {
    if (isOpen) {
      setColor('transparent')
    } else {
      setColor('dark')
    }
    setIsOpen(!isOpen)
  }

  function dropdownNotificationsToggle () {
    setDropdownNotificationsOpen(!dropdownNotificationsOpen)
  }

  function dropdownOptionsToggle () {
    setDropdownOptionsOpen(!dropdownOptionsOpen)
  }

  return (
    <Navbar expand="lg" color={color} className={'navbar-absolute fixed-top ' + (color === 'transparent' ? 'navbar-transparent' : '')}>
      <Container fluid>
        <div className="navbar-wrapper">
          <div className="navbar-toggle">
            <button
              type="button"
              ref={sidebarToggle}
              className="navbar-toggler"
              onClick={openSidebar}
            >
              <span className="navbar-toggler-bar bar1"></span>
              <span className="navbar-toggler-bar bar2"></span>
              <span className="navbar-toggler-bar bar3"></span>
            </button>
          </div>
          <div className="navbar-brand">
            {pageTitle}
            &nbsp;
            {pageTitleOptions}
          </div>
        </div>
        <NavbarToggler onClick={toggle}>
          <span className="navbar-toggler-bar navbar-kenab"></span>
          <span className="navbar-toggler-bar navbar-kenab"></span>
          <span className="navbar-toggler-bar navbar-kenab"></span>
        </NavbarToggler>
        <Collapse
          isOpen={isOpen}
          navbar
          className="justify-content-end"
        >
          <form>
            <InputGroup className="no-border">
              <Input placeholder="Search..." />
              <InputGroupAddon addonType="append">
                <InputGroupText>
                  <i className="nc-icon nc-zoom-split" />
                </InputGroupText>
              </InputGroupAddon>
            </InputGroup>
          </form>
          <Nav navbar>
            <NavItem>
              <Link to="#test" className="nav-link btn-magnify">
                <i className="nc-icon nc-layout-11" />
                <p>
                  <span className="d-lg-none d-md-block">Stats</span>
                </p>
              </Link>
            </NavItem>
            <Dropdown nav isOpen={dropdownNotificationsOpen} toggle={dropdownNotificationsToggle}>
              <DropdownToggle caret nav>
                <i className="nc-icon nc-bell-55" />
                <p>
                  <span className="d-lg-none d-md-block">Some actions</span>
                </p>
              </DropdownToggle>
              <DropdownMenu right className="no-transition">
                <DropdownItem tag="a">Action</DropdownItem>
                <DropdownItem tag="a">Another Action</DropdownItem>
              </DropdownMenu>
            </Dropdown>
            <Dropdown nav isOpen={dropdownOptionsOpen} toggle={dropdownOptionsToggle}>
              <DropdownToggle caret nav>
                <i className="nc-icon nc-settings-gear-65" />
                <p>
                  <span className="d-lg-none d-md-block">Options</span>
                </p>
              </DropdownToggle>
              <DropdownMenu right className="no-transition">
                <DropdownItem onClick={() => history.push('/dashboard/my-profile')}>My profile</DropdownItem>
                <DropdownItem onClick={() => signOut()}>Logout</DropdownItem>
              </DropdownMenu>
            </Dropdown>
          </Nav>
        </Collapse>
      </Container>
    </Navbar>
  )
}
