import React from 'react'
import { Nav } from 'reactstrap'
import { useProjectId } from '../../../projects/hooks'
import SidebarLink from '../sidebar-link'
import { Link } from 'react-router-dom'

export default function Sidebar () {
  const projectId = useProjectId()

  const links = [
    {
      path: '/dashboard',
      name: 'Select project',
      icon: 'nc-icon nc-bank'
    }
  ]

  if (projectId) {
    links.push(...makeProjectLinks(projectId))
  }

  return (
    <div className="sidebar">
      <div className="logo">
        <Link className="simple-text logo-normal sidebar-title" to="/dashboard">
          <img src="/images/logo.svg" height="30" alt="a" />
          &nbsp; Agilify It
        </Link>
      </div>
      <div className="sidebar-wrapper">
        <Nav>
          {links.map((link, key) => (
            <SidebarLink link={link} key={key} />
          ))}
        </Nav>
      </div>
    </div>
  )
}

function makeProjectLinks (projectId) {
  return [
    {
      path: `/dashboard/projects/${projectId}`,
      name: 'Overview',
      type: 'normal',
      icon: 'nc-icon nc-layout-11'
    },
    {
      path: `/dashboard/projects/${projectId}/backlog`,
      name: 'Backlog',
      type: 'backlog',
      icon: 'nc-icon nc-tile-56'
    },
    {
      path: `/dashboard/projects/${projectId}/sprints/current`,
      name: 'Sprint',
      type: 'sprint',
      icon: 'nc-icon nc-refresh-69'
    }
  ]
}
