import React, { Suspense, lazy, useState } from 'react'
import { Switch, Route, Redirect } from 'react-router-dom'
import Page from '../../../theme/components/page'
import LoadingContent from '../../../theme/components/loading-content'
import { useSelector } from 'react-redux'
import DashboardContext from './context'
import jwt from 'jsonwebtoken'
import { current } from '../../../users/services/user-service'

const ProjectSelectorPage = lazy(() => import('../../../projects/components/selector-page'))
const ProjectFormPage = lazy(() => import('../../../projects/components/form-page'))
const OverviewPage = lazy(() => import('../../../overview/components/overview-page'))
const BacklogPage = lazy(() => import('../../../boards/components/backlog-page'))
const CurrentSprintPage = lazy(() => import('../../../sprints/components/current-sprint-page'))
const SprintPage = lazy(() => import('../../../sprints/components/sprint-page'))
const SprintFormPage = lazy(() => import('../../../sprints/components/sprint-form-page'))
const MyProfilePage = lazy(() => import('../../../users/components/my-profile-page'))

export default function Dashboard () {
  const auth = useSelector(state => state.auth)

  const userId = useSelector(state => {
    if (!state.auth) {
      return null
    }

    const data = jwt.decode(state.auth.access_token)

    return data.sub
  })

  const context = {
    userId
  }

  if (!auth) {
    return <Redirect to='/login' />
  }

  return (
    <DashboardContext.Provider value={context}>
      <Page>
        <Suspense fallback={<LoadingContent />}>
          <Switch>
            <Route path="/dashboard/projects/create" component={ProjectFormPage} />
            <Route path="/dashboard/projects/:projectId/edit" component={ProjectFormPage} />
            <Route path="/dashboard/projects/:projectId/backlog" component={BacklogPage} />
            <Route path="/dashboard/projects/:projectId/sprints/current" component={CurrentSprintPage} />
            <Route path="/dashboard/projects/:projectId/sprints/create" component={SprintFormPage} />
            <Route path="/dashboard/projects/:projectId/sprints/:sprintId/edit" component={SprintFormPage} />
            <Route path="/dashboard/projects/:projectId/sprints/:sprintId" component={SprintPage} />
            <Route path="/dashboard/projects/:projectId" component={OverviewPage} />
            <Route path="/dashboard/my-profile" component={MyProfilePage} />
            <Route path="/dashboard" component={ProjectSelectorPage} />
          </Switch>
        </Suspense>
      </Page>
    </DashboardContext.Provider>
  )
}
