import React, { Suspense, lazy } from 'react'
import './css/style.scss'
import Navbar from '../navbar'
import Footer from '../footer'
import HomeCover from '../home-cover'
import LoadingCover from '../loading-cover'
import { Switch, Route } from 'react-router-dom'

const AboutPage = lazy(() => import('../about-page'))
const ContactPage = lazy(() => import('../contact-page'))
const SignupPage = lazy(() => import('../signup-page'))
const LoginPage = lazy(() => import('../login-page'))
const ForgetMyPasswordPage = lazy(() => import('../forget-my-password-page'))
const ResetPasswordPage = lazy(() => import('../reset-password-page'))

export default function Home () {
  return (
    <div className="app-home">
      <Navbar />
      <Suspense fallback={<LoadingCover />}>
        <Switch>
          <Route path="/about" component={AboutPage} />
          <Route path="/contact" component={ContactPage} />
          <Route path="/signup" component={SignupPage} />
          <Route path="/login" component={LoginPage} />
          <Route path="/forget-my-password" component={ForgetMyPasswordPage} />
          <Route path="/reset-password/:token" component={ResetPasswordPage} />
          <Route path="/" component={HomeCover} />
        </Switch>
      </Suspense>
      <Footer />
    </div>
  )
}
