import React from 'react'
import Home from '../../../home/components/home'
import Dashboard from '../../../dashboard/app/components/dashboard'
import store from '../../store'
import { Provider } from 'react-redux'
import {
  BrowserRouter as Router,
  Switch,
  Route
} from 'react-router-dom'
import Backend from 'react-dnd-html5-backend'
import { DndProvider } from 'react-dnd'
import Container from '../../container'
import config from '../../../../config'
import * as wsService from '../../services/ws-service'
import firebase from 'firebase'
import * as firebaseui from 'firebaseui'

firebase.initializeApp({
  apiKey: 'AIzaSyBC1wAi7YZ-Wyubu2NnOjCVDfyoqA8lV1o',
  authDomain: 'agileful.firebaseapp.com'
})

// Initialize the FirebaseUI Widget using Firebase.
const authUi = new firebaseui.auth.AuthUI(firebase.auth())

export default function App () {
  const container = {
    ws: wsService,
    config,
    authUi
  }

  return (
    <Provider store={store}>
      <DndProvider backend={Backend}>
        <Container.Provider value={container}>
          <Router>
            <Switch>
              <Route path="/dashboard/projects/:projectId" component={Dashboard} />
              <Route path="/dashboard" component={Dashboard} />
              <Route path="/" component={Home} />
            </Switch>
          </Router>
        </Container.Provider>
      </DndProvider>
    </Provider>
  )
}
