import { combineReducers } from 'redux'
import { SET_AUTH, LOGOUT } from './actions'

const authInitialState = null

function auth (state = authInitialState, action) {
  switch (action.type) {
    case SET_AUTH:
      return action.auth
    case LOGOUT:
      return null
    default:
      return state
  }
}

const rootReducer = combineReducers({
  auth
})

export default rootReducer
