import { request } from '../../../core/client'
import { serialize, deserialize } from 'jsonapi-fractal'

export async function current () {
  let response

  try {
    response = await request({
      method: 'GET',
      url: '/users/current'
    })
  } catch (e) {
    throw new Error(e.response.data.errors[0].detail)
  }

  return deserialize(response.data)
}

export async function updateCurrent (user) {
  try {
    const response = await request({
      method: 'PATCH',
      url: '/users/current',
      data: serialize(user, 'users')
    })

    return deserialize(response.data)
  } catch (e) {
    throw new Error(e.response.data.errors[0].detail)
  }
}
