import { request } from '../../../core/client'
import { deserialize } from 'jsonapi-fractal'
import { moveToSprint, moveToBacklog } from '../../tasks/services/task-service'

export async function list (projectId, filter) {
  const params = []

  if (filter && 'type' in filter) {
    params.push('filter[type]=' + filter.type)
  }

  const response = await request({
    method: 'GET',
    url: `/dashboard/projects/${projectId}/boards` + (params.length > 0 ? '?' + params.join('&') : '')
  })

  return deserialize(response.data)
}

export async function get (projectId, id) {
  const response = await request({
    method: 'GET',
    url: `/dashboard/projects/${projectId}/boards/${id}`
  })

  return deserialize(response.data)
}

export function moveTaskToBoardType (projectId, task, boardType) {
  if (boardType === 'sprint') {
    return moveToSprint(projectId, task.board.id, task.id)
  }

  if (boardType === 'backlog') {
    return moveToBacklog(projectId, task.board.id, task.id)
  }
}
