import React from 'react'
import { useDrop } from 'react-dnd'
import { ItemTypes } from '../../../app/dnd'
import { Link, useLocation } from 'react-router-dom'
import styled from 'styled-components'
import { useProjectId } from '../../../projects/hooks'
import { moveTaskToBoardType } from '../../../boards/services/board-service'

export default function SidebarLink ({ link }) {
  const location = useLocation()
  const projectId = useProjectId()
  const isDropable = link.type === 'sprint' || link.type === 'backlog'

  let name = link.name
  let dropFocus = false

  const [dropProps, dropRef] = useDrop({
    accept: [ItemTypes.TASK],
    collect (monitor, props) {
      return {
        isOver: monitor.isOver()
      }
    },
    drop (item, monitor) {
      if (isDropable) {
        moveTaskToBoardType(projectId, item, link.type)
      }
    }
  })

  if (dropProps.isOver && isDropable) {
    name = 'Move to ' + name
    dropFocus = true
  }

  const classes = []

  if (isActive(link.path, location)) {
    classes.push('active')
  }

  if (dropFocus) {
    classes.push('drop-focus')
  }

  return (
    <SidebarLinkLi ref={dropRef} className={classes.join(' ')}>
      <Link to={link.path} className={'nav-link' + (isActive(link.path, location) ? ' active' : '')}>
        <i className={link.icon}></i>
        <p>{name}</p>
      </Link>
    </SidebarLinkLi>
  )
}

function isActive (path, location) {
  if (path === '/dashboard' || location.path === '/dashboard') {
    return location.pathname === path
  }
  return location.pathname === path
}

const SidebarLinkLi = styled.li`
  &.drop-focus {
    outline: 3px solid white;
  }
`
