import { request, serializeQueryString } from '../../../core/client'
import { serialize, deserialize } from 'jsonapi-fractal'

export async function list (projectId, boardId, opts) {
  const queryString = {}

  if (opts.fields) {
    queryString.fields = opts.fields
  }

  const append = queryString ? '?' + serializeQueryString(queryString) : ''

  const response = await request({
    method: 'GET',
    url: `/dashboard/projects/${projectId}/boards/${boardId}/tasks${append}`
  })

  return deserialize(response.data)
}

export async function get (projectId, boardId, id, queryString) {
  const queryStringText = queryString ? serializeQueryString(queryString) : null
  const append = queryStringText ? '?' + queryStringText : ''

  const response = await request({
    method: 'GET',
    url: `/dashboard/projects/${projectId}/boards/${boardId}/tasks/${id}` + append
  })

  return deserialize(response.data)
}

export async function create (projectId, boardId, task) {
  let response

  try {
    response = await request({
      method: 'POST',
      url: `/dashboard/projects/${projectId}/boards/${boardId}/tasks`,
      data: serializeTask(task)
    })
  } catch (e) {
    throw new Error(e.response.data.errors[0].detail)
  }

  return deserialize(response.data)
}

export async function edit (projectId, boardId, id, task) {
  let response

  try {
    response = await request({
      method: 'PATCH',
      url: `/dashboard/projects/${projectId}/boards/${boardId}/tasks/${id}`,
      data: serializeTask(task)
    })
  } catch (e) {
    throw new Error(e.response.data.errors[0].detail)
  }

  return deserialize(response.data)
}

export async function destroy (projectId, boardId, id) {
  await request({
    method: 'DELETE',
    url: `/dashboard/projects/${projectId}/boards/${boardId}/tasks/${id}`
  })
}

export async function moveToSprint (projectId, boardId, id) {
  const response = await request({
    method: 'POST',
    url: `/dashboard/projects/${projectId}/boards/${boardId}/tasks/${id}/move-to-sprint`
  })

  return deserialize(response.data)
}

export async function moveToBacklog (projectId, boardId, id) {
  const response = await request({
    method: 'POST',
    url: `/dashboard/projects/${projectId}/boards/${boardId}/tasks/${id}/move-to-backlog`
  })

  return deserialize(response.data)
}

function serializeTask (task) {
  const relationships = []

  if (task.column) {
    relationships.push('column')
  }

  return serialize(task, 'tasks', {
    relationships
  })
}
