import escapeStringRegexp from 'escape-string-regexp'
import Pusher from 'pusher-js/with-encryption'
import config from '../../../config'

const pusher = new Pusher(config.pusher.key, {
  cluster: config.pusher.cluster
})

const channels = {}
const listeners = {}

export function join (room) {
  const channel = pusher.subscribe(room)
  channel.patternListeners = {}
  channels[room] = channel

  channel.bind_global((event, data) => {
    for (const k in listeners) {
      const listener = listeners[k]

      if (listener.room === room && listener.regex.test(event)) {
        try {
          listener.callback(data)
        } catch (e) {
          console.error(e)
        }
      }
    }
  })
}

export function leave (room) {
  pusher.unsubscribe(room)
  delete channels[room]
}

export function onPattern (pattern, callback) {
  const [room] = pattern.split('/')

  const id = uniqid()
  const regex = new RegExp(escapeStringRegexp(pattern.replace('*', 'WILDCARD')).replace('WILDCARD', '.*'))

  listeners[id] = { room, regex, callback }

  return function () {
    try {
      delete listeners[id]
    } catch (e) {
      console.error(e)
    }
  }
}

function uniqid () {
  return Math.random() * 10000000000000000
}
