import axios from 'axios'
import config from '../../config'
import store from './store'
import { refreshToken } from './services/auth-service'
import { setAuth } from './store/actions'

const client = axios.create({
  baseURL: config.api.url
})

export async function request (params, tries = 1) {
  const auth = store.getState().auth
  try {
    params.headers = getHeaders()
    const response = await client(params)
    return response
  } catch (e) {
    if (/\/auth/.test(params.url)) {
      throw e
    }

    if (auth && 'response' in e && e.response.status === 401 && tries < 2) {
      try {
        await refreshToken(auth.refresh_token)
      } catch (e2) {
        if ('response' in e2 && e2.response.status === 401) {
          store.dispatch(setAuth(null))
        }
        throw e
      }

      const response = await request(params, tries + 1)
      return response
    }

    throw e
  }
}

function getHeaders () {
  const headers = {}
  const auth = store.getState().auth

  if (auth) {
    headers.Authorization = 'Bearer ' + auth.access_token
  }

  return headers
}

export function serializeQueryString (obj, prefix) {
  var str = []
  for (const p in obj) {
    if (Object.prototype.hasOwnProperty.call(obj, p)) {
      var k = prefix ? prefix + '[' + p + ']' : p
      var v = obj[p]
      str.push((v !== null && typeof v === 'object')
        ? serializeQueryString(v, k)
        : encodeURIComponent(k) + '=' + encodeURIComponent(v))
    }
  }
  return str.join('&')
}
